.App {
  text-align: center;
  height: 100vh;
  overflow: scroll;
}

.light{
  --bg: white;
  --text: black;
}

.dark{
  --bg: black;
  --text: black;
}

.controls{
  position: fixed;
  bottom: 0;
  height: 8vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content:space-around;
  
  overflow: hidden;
  box-shadow: rgba(100, 100, 111, 0.5) 0px 7px 29px 0px;
  background-color: white;
}

.control{
  background: none;
  border: none;
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  transition: 500ms;
}

.control:active{
  background-color: rgb(224, 214, 182);
}

.s1{
  position: relative;
  height: 150vh;
}

.s1 p{
  font-size: 15px;
  text-align: left;
  padding: 20px;
  font-family: 'Poppins';
  font-weight: 300;
}

